import React, { useEffect } from 'react'
import { View, Image, Text, Video, RichText } from '@tarojs/components'
import styles from './index.module.less'
import { toJS } from 'mobx'
import { useObserver } from 'mobx-react'
import Taro, { useRouter, useShareAppMessage, useShareTimeline } from '@tarojs/taro'
import CircleProcess from '@/components/CircleProcess'
import useStore from '../../../../util/useStore'
import getHMS from '../../../../util/getHMS'
import EmptyList from '../../../../components/Hunan/EmptyList'
import getCurrentUrl from '../../../../util/getCurrentUrl'

const Detail = props => {
  const router = useRouter()
  let { params } = router || {}
  let { id } = params || {}

  const { store } = useStore()
  let { globalStore } = store || {}
  let { defaultBannerImage, defaultLogo } = globalStore || {}

  useEffect(() => {
    let { getLessonDetail, getLessonChapters, cleanAll } = props
    getLessonDetail = getLessonDetail || new Function()
    getLessonChapters = getLessonChapters || new Function()
    getLessonDetail(id)
    getLessonChapters(id)
    Taro.eventCenter.on('toggleFollow', setFollowStatus)
    const cleanUp = () => {
      Taro.eventCenter.off('toggleFollow', setFollowStatus)
    }
    return cleanUp
  }, [])

  let shareObj = {}

  useShareAppMessage(res => {
    return shareObj
  })
  useShareTimeline(res => {
    return shareObj
  })

  const setFollowStatus = bool => {
    let { upDateFollow } = props
    upDateFollow = upDateFollow || new Function()
    upDateFollow(bool)
  }

  /**
   * 根据学习状态生成dom
   * **/
  const getLearnStatus = (id, process) => {
    process = Number(process || 0)
    if (process === 0) {
      return <View className={styles.unlearn_sign} />
    } else if (process === 100) {
      return <View className={styles.learned_sign} />
    } else {
      return <CircleProcess id={id} process={process} classname={styles.canvas} />
    }
  }

  return useObserver(() => {
    let {
      info,
      selItem,
      listData,
      selSee,
      toStudy,
      fixErrorImage,
      fixErrorAvatar,
      toggleUnit,
      toggleFollow,
      testData,
      toggleSelTest,
      goToTest,
      sel_test,
      test_id,
    } = props
    listData = listData || []
    testData = testData || {}
    selSee = selSee || new Function()
    toStudy = toStudy || new Function()
    fixErrorImage = fixErrorImage || new Function()
    fixErrorAvatar = fixErrorAvatar || new Function()
    toggleUnit = toggleUnit || new Function()
    toggleFollow = toggleFollow || new Function()
    toggleSelTest = toggleSelTest || new Function()
    goToTest = goToTest || new Function()
    sel_test = sel_test || false
    let {
      title,
      image,
      price,
      cost_price,
      total_time,
      total_video,
      click,
      category,
      map,

      description,
      comment,
      target,
      content,
      source,
      is_follow,
      is_task,
    } = info || {}

    image = image || defaultBannerImage
    category = category || {}
    map = map || {}
    content = content || {}
    content.content = content.content || ''
    content.content = content.content
      .replace(/<p([\s\w"=\/\.:;]+)((?:(style="[^"]+")))/gi, '<p')
      .replace(/<p([\s\w"=\/\.:;]+)((?:(class="[^"]+")))/gi, '<p')
      .replace(/<p>/gi, '<p style="textIndent:60px;marginBottom:20px">')
      .replace(/<img([\s\w"-=\/\.:;]+)((?:(height="[^"]+")))/gi, '<img$1')
      .replace(/<img([\s\w"-=\/\.:;]+)((?:(width="[^"]+")))/gi, '<img$1')
      .replace(/<img([\s\w"-=\/\.:;]+)((?:(style="[^"]+")))/gi, '<img$1')
      .replace(/<img([\s\w"-=\/\.:;]+)((?:(alt="[^"]+")))/gi, '<img$1')
      .replace(/<img/gi, '<img width="100%" height="auto" style="margin-bottom:10px;display:block"')
    source = source || {}
    let { seller } = source
    seller = seller || {}
    source.avatar = source.avatar || defaultLogo
    is_follow = is_follow || false
    is_task = is_task || false

    shareObj = {
      title,
      path: getCurrentUrl(),
      imageUrl: image,
    }

    const { store } = useStore()
    let { globalStore } = store || {}
    let { userData } = toJS(globalStore) || {}
    userData = userData || {}
    let userInfo = userData.info
    let { real_auth_status } = userInfo || {}
    real_auth_status = Number(real_auth_status || 0)

    return (
      <View className={styles.page}>
        <Image
          lazyLoad={true}
          src={image}
          className={styles.video_content}
          onError={() => {
            fixErrorImage(defaultBannerImage)
          }}
        />

        <View className={styles.info_content}>
          <View className={styles.info}>
            <View className={styles.name}>{title}</View>
          </View>

          <View className={styles.fav} onClick={toggleFollow}>
            <View className={[is_follow ? styles.signed : styles.sign].join(' ')} />
            <View className={styles.text}>收藏</View>
          </View>
        </View>
        <View className={styles.gray_con} />
        <View className={styles.tabs}>
          <View
            className={[styles.tab_item, selItem === 1 ? styles.active : ''].join(' ')}
            onClick={() => {
              selSee(1)
            }}
          >
            学习目录
          </View>
          <View
            className={[styles.tab_item, selItem === 2 ? styles.active : ''].join(' ')}
            onClick={() => {
              selSee(2)
            }}
          >
            课程详情
          </View>
          <View className={[styles.tab_line, selItem === 2 ? styles.active : ''].join(' ')} />
        </View>
        {/*学习目录*/}
        {selItem === 1 ? (
          <View className={styles.list}>
            {listData.length === 0 && Object.keys(testData).length === 0 ? <EmptyList /> : null}

            {listData.map((item, listIndex) => {
              let { id, title, sections, units, active } = item
              sections = sections || []
              units = units || []
              return (
                <View
                  key={`chapter_${id}`}
                  className={[styles.chapter, active ? styles.active : ''].join(' ')}
                  onClick={e => {
                    e.stopPropagation()
                    toggleUnit(item)
                  }}
                >
                  <View className={styles.chapter_info}>
                    <View className={styles.chapter_sign} />
                    <View className={styles.chapter_tit}>{title}</View>
                  </View>
                  {sections.map(sectionItem => {
                    let { title, units } = sectionItem || {}
                    units = units || []
                    return (
                      <View key={`section_unit_${sectionItem.id}`}>
                        <View className={styles.section}>{title}</View>
                        {units.map(sectionUnit => {
                          let { type, typeName, title, total_time, total_question, isFirstMovie } =
                            sectionUnit || {}
                          type = Number(type || 0)
                          typeName = typeName || {}
                          return (
                            <View
                              className={styles.section_unit}
                              key={`section_unit_${sectionUnit.id}`}
                              onClick={e => {
                                toStudy(e, isFirstMovie, is_task, sectionUnit, real_auth_status)
                              }}
                            >
                              <View className={styles.section_unit_info_content}>
                                <View className={styles.section_unit_sign}>{typeName.name}</View>
                                <View className={styles.section_unit_info}>
                                  <View className={styles.section_unit_info_left}>
                                    <View className={styles.section_unit_info_left_title}>
                                      <View className={styles.title}>{title}</View>
                                      {isFirstMovie ? (
                                        <View className={styles.title_sign}>试看</View>
                                      ) : null}
                                    </View>
                                    {type === 1 ? (
                                      <View className={styles.section_unit_info_left_info}>
                                        <View className={styles.unit_time} />
                                        <View className={styles.unit_time_str}>
                                          {getHMS(total_time)}
                                        </View>
                                      </View>
                                    ) : null}
                                    {type === 2 ? (
                                      <View className={styles.section_unit_info_left_info}>
                                        <View className={styles.unit_practise} />
                                        <View className={styles.unit_time_str}>
                                          {total_question}题
                                        </View>
                                      </View>
                                    ) : null}
                                  </View>

                                  {!isFirstMovie ? (
                                    <View className={styles.section_unit_info_right} />
                                  ) : null}
                                </View>
                              </View>
                            </View>
                          )
                        })}
                      </View>
                    )
                  })}
                  {units.map(chapterUnit => {
                    let { type, typeName, title, total_time, total_question, isFirstMovie } =
                      chapterUnit || {}
                    type = Number(type || 0)
                    typeName = typeName || {}
                    return (
                      <View
                        key={`chapter_unit_${chapterUnit.id}`}
                        className={styles.section_unit}
                        onClick={e => {
                          toStudy(e, isFirstMovie, is_task, chapterUnit, real_auth_status)
                        }}
                      >
                        <View className={styles.section_unit_info_content}>
                          <View className={styles.section_unit_sign}>{typeName.name}</View>
                          <View className={styles.section_unit_info}>
                            <View className={styles.section_unit_info_left}>
                              <View className={styles.section_unit_info_left_title}>
                                <View className={styles.title}>{title}</View>
                                {isFirstMovie ? (
                                  <View className={styles.title_sign}>试看</View>
                                ) : null}
                              </View>
                              {type === 1 ? (
                                <View className={styles.section_unit_info_left_info}>
                                  <View className={styles.unit_time} />
                                  <View className={styles.unit_time_str}>{getHMS(total_time)}</View>
                                </View>
                              ) : null}
                              {type === 2 ? (
                                <View className={styles.section_unit_info_left_info}>
                                  <View className={styles.unit_practise} />
                                  <View className={styles.unit_time_str}>{total_question}题</View>
                                </View>
                              ) : null}
                            </View>

                            {!isFirstMovie ? (
                              <View className={styles.section_unit_info_right} />
                            ) : null}
                          </View>
                        </View>
                      </View>
                    )
                  })}
                </View>
              )
            })}

            {Object.keys(testData).length !== 0 ? (
              <View
                className={[styles.chapter, sel_test ? styles.active : ''].join(' ')}
                onClick={e => {
                  e.stopPropagation()
                  toggleSelTest()
                }}
              >
                <View className={styles.chapter_info}>
                  <View className={styles.chapter_sign} />
                  <View className={styles.chapter_tit}>课后考试</View>
                </View>

                <View
                  className={[styles.section_unit, test_id ? styles.active : ''].join(' ')}
                  onClick={e => {
                    e.stopPropagation()
                    goToTest()
                  }}
                >
                  <View className={styles.section_unit_info_content}>
                    <View className={styles.section_unit_sign}>考试</View>
                    <View className={styles.section_unit_info}>
                      <View className={styles.section_unit_info_left}>
                        <View className={styles.section_unit_info_left_title}>
                          <View
                            className={[
                              styles.title,
                              Number(test_id || 0) !== 0 ? styles.active : '',
                            ].join(' ')}
                          >
                            {testData.title}
                          </View>
                        </View>
                      </View>
                      <View className={styles.section_unit_info_right} />
                    </View>
                  </View>
                </View>
              </View>
            ) : null}
          </View>
        ) : null}

        {/*学习详情*/}
        {selItem === 2 ? (
          <View className={styles.detail}>
            <Text className={styles.des_title}>课程概述</Text>
            <View className={styles.des_txt}>{description}</View>

            <Text className={styles.des_title}>课程目标</Text>
            <View className={styles.des_txt}>{target}</View>

            <Text className={styles.des_title}>考核评价</Text>
            <View className={styles.des_txt}>{comment}</View>

            <Text className={styles.des_title}>详细介绍</Text>
            <RichText className={styles.rich_content} nodes={content.content} />

            <Text className={styles.des_title}>课程来源</Text>
            <View className={styles.from}>
              <Image
                lazyLoad={true}
                className={styles.icon}
                src={source.avatar}
                onError={() => {
                  fixErrorAvatar(defaultLogo)
                }}
              />
              <Text className={styles.txt}>{seller.company_alias_name}</Text>
            </View>
          </View>
        ) : null}
      </View>
    )
  })
}

export default Detail
