import React from 'react'
import { observable, toJS } from 'mobx'
import Taro from '@tarojs/taro'
import { myGetData, myDeleteData, myPostData } from '../../../servers/http'
import api from '../api'

class detailStore {
  // 课程id
  @observable id = null
  // 课程详情
  @observable info = {}
  @observable listData = []
  @observable testData = {}
  @observable canRequest = true
  @observable sel_test = true
  @observable test_id = null
  @observable selItem = 1
  selSee = type => {
    this.selItem = type
  }
  //打开考试章节
  toggleSelTest = () => {
    this.sel_test = !this.sel_test
  }
  //前去考试
  goToTest = () => {
    let { id } = this.testData || {}
    this.test_id = id
    Taro.showToast({
      title: '您目前只能观看试看视频',
      icon: 'none',
      duration: 3000,
    })
  }

  cleanAll = () => {
    // 课程详情
    this.info = {}
    this.listData = []
    this.testData = {}
    this.sel_test = true
    this.test_id = null
    this.canRequest = true
    this.selItem = 1
  }

  /**
   * 更新收藏状态**/
  upDateFollow = bool => {
    bool = bool || false
    const temp = JSON.parse(JSON.stringify(this.info))
    temp.is_follow = bool
    this.info = temp
  }

  /**
   * 根据数据跳转学习页
   * **/
  toStudy = (e, isFirstMovie, is_task, data, realStatus) => {
    e.stopPropagation()
    const { id } = data
    if (isFirstMovie) {
      Taro.navigateTo({
        url: `/pages/lesson/study/index?cid=${this.id}&unitid=${id}`,
      })
    } else {
      Taro.showToast({
        title: '您目前只能观看试看视频',
        icon: 'none',
        duration: 3000,
      })
    }
  }

  /**
   * 开始学习
   * */
  startStudy = (video_id, time, type) => {
    let params = { video_id, time }
    switch (type) {
      case 'start':
        params.start = 1
        break
      case 'end':
        params.end = 1
    }
    myPostData(api.studies, params)
      .then(({ status, data }) => {})
      .catch(e => {})
  }

  toggleFollow = () => {
    if (!this.canRequest) return
    this.canRequest = false
    let { is_follow } = this.info
    is_follow = is_follow || false
    if (is_follow) {
      myDeleteData(`${api.follows}/${this.id}`)
        .then(({ status, data }) => {
          this.canRequest = true
          if (status !== 204) return
          const temp = JSON.parse(JSON.stringify(this.info))
          temp.is_follow = false
          this.info = temp
        })
        .catch(() => {
          this.canRequest = true
        })
    } else {
      myPostData(api.follows, { follow_id: this.id })
        .then(({ status, data }) => {
          this.canRequest = true
          if (status !== 201) return
          const temp = JSON.parse(JSON.stringify(this.info))
          temp.is_follow = true
          this.info = temp
        })
        .catch(() => {
          this.canRequest = true
        })
    }
  }

  /**获取课程详情*/
  getLessonDetail = async id => {
    this.id = id
    myGetData(`${api.courses}/${id}`)
      .then(({ status, data }) => {
        if (status !== 200) return
        this.info = data
      })
      .catch(({ status, data }) => {})
  }

  /**获取课程目录*/
  getLessonChapters = async id => {
    this.id = id
    myGetData(`${api.newCourseChapters}`, { course_id: id })
      .then(({ status, data }) => {
        if (Number(status || 0) > 199 && Number(status || 0) < 300) {
          let { course, test } = data || {}
          course = course || []
          test = test || {}
          let tempAllUnit = []
          course.map((a, index) => {
            a.active = true
            let { sections, units } = a || {}
            sections = sections || []
            units = units || []
            sections.map(s => {
              let { units } = s || {}
              units.map(u => {
                tempAllUnit.push(u)
                u.active = false
              })
            })
            units.map(u => {
              tempAllUnit.push(u)
              u.active = false
            })
          })
          tempAllUnit.every(item => {
            if (Number(item.type || 0) === 1) {
              item.isFirstMovie = true
              return false
            } else {
              return true
            }
          })
          this.listData = course
          this.testData = test
        }
      })
      .catch(({ status, data }) => {})
  }

  /**显示隐藏章节视频*/
  toggleUnit = data => {
    let tempList = JSON.parse(JSON.stringify(this.listData))
    tempList.map(item => {
      if (item.id.toString() === data.id.toString()) {
        item.active = !item.active
      }
    })
    this.listData = tempList
  }

  /**
   * 图片资源失败时更换为默认图
   * @param index 索引
   * @param defaultImage 默认图
   * */
  fixErrorImage = defaultImage => {
    let tempInfo = JSON.parse(JSON.stringify(this.info))
    if (tempInfo) {
      tempInfo.image = defaultImage
    }
    this.info = tempInfo
  }

  /**
   * 图片资源失败时更换为默认图
   * @param index 索引
   * @param defaultImage 默认图
   * */
  fixErrorAvatar = defaultImage => {
    let tempInfo = JSON.parse(JSON.stringify(this.info))
    if (tempInfo) {
      tempInfo.source = tempInfo.source || {}
      tempInfo.source.avatar = defaultImage
    }
    this.info = tempInfo
  }
}

export default new detailStore()
